import React, {useCallback, useEffect, useState} from 'react';
import basketImg from '../assets/img/basket.svg'
import axios from "axios";
import {logDOM} from "@testing-library/react";

const Basket = ({productBasket ,setProductBasket}) => {

    const [isOpen, setIsOpen] = useState(false)
    let [priceBasket, setPriceBasket] = useState(0)
    const [prodB, setProdB] = useState([])
    // const totalPrice = productBasket.reduce((sum, obj) => obj.price + sum, 0); .... старая версия

    useEffect(() => {

        let priceSum = 0;
        productBasket.forEach(e => {
            priceSum += e.price
        })
        setPriceBasket(priceSum)



    }, [productBasket,  setProdB])


    const onRemoveItem = (prod) => {
        prod.amount--
        setProdB([...prodB, prod])
        setPriceBasket(priceBasket -= prod.price)
         if (prod.amount < 1){
            axios.delete(`https://6378a8e37eb4705cf2717c2a.mockapi.io/cart/${prod.id}`)
            setProductBasket((prev) => prev.filter(item => Number(item.id) !== Number(prod.id)))
        }
    }

    const addThisItem = (prod) => {
            setProdB([...prodB, prod])
            prod.amount++
            setPriceBasket(priceBasket += prod.price)
            // totalPrice += 100
    }

    const orderProducts = () => {
        if (productBasket.length){
            setProductBasket([])
            productBasket.forEach(e => {
                axios.delete(`https://6378a8e37eb4705cf2717c2a.mockapi.io/cart/${e.id}`)
            })
            alert('Заказ оформлен. Ждите!')
        }
        else{
            alert('У вас нет заказа, добавьте заказ')
        }
    }

    return (
        <div className='basket'>
            <div className="basket__head" onClick={() => setIsOpen(!isOpen)}>
                <div className="price">{priceBasket} грн.</div>
                <div className="block-basket">
                    {productBasket.length
                        ? <div className="block-basket-length">{productBasket.length}</div>
                        : ''
                    }
                    <img src={basketImg} alt="basket"/>
                </div>
                <div className={["basket__arrow", isOpen ? 'active' : ''].join(' ')}></div>
            </div>
            {isOpen &&
                <div className='basket__products'>
                    {
                        productBasket.length
                        ?     <h2>КОРЗИНА</h2>
                        :    <h2>ПУСТО</h2>
                    }
                    <div className="products">
                        {
                            productBasket.map((item , idx) =>
                                <div className='basket__card' key={idx}>
                                    <div className='image'>
                                        <img src={process.env.PUBLIC_URL + item.img} alt="image"/>
                                    </div>
                                    <div className="content">
                                        <h3>{item.title}</h3>
                                        <a href="/podrob">подробнее</a>
                                    </div>
                                    <div className="amount">
                                        <button className="btn plus" onClick={() => addThisItem(item)}>+</button>
                                        {item.amount}
                                        <button className='btn minus' onClick={() => onRemoveItem(item)}>-</button>
                                    </div>
                                </div>
                            )
                        }
                    </div>
                    <div className="basket__products-order">
                        <div className="basket__product-sum">
                            сумма - <span>{priceBasket} грн</span>
                        </div>
                        <button onClick={orderProducts} className="btn" >оформить заказ</button>
                    </div>
                </div>
            }
        </div>
    );
};

export default Basket;