import React from 'react';
import LoaderCard from "./LoaderCard";

const Card = ({ onPlus, item, isLoading}) => {

    const onClickPlus = (e) => {
        onPlus(item)
    }
    return (
        <div className='block-item'>
            {
                isLoading
                    ?
                    <LoaderCard/>
                    :
                    <>
                        <div className="img__item"><img src={process.env.PUBLIC_URL + item.img} alt=""/></div>
                        <h3>{item.title}</h3>
                        <p>{item.text}</p>
                        <div className='other__item'>
                            <div className="prices__item">
                                { item.price > 0
                                    ? <div className="price__item">
                                        {item.price} грн.
                                    </div>
                                    : ''
                                }
                                <div className={["height__item", item.price ? 'heightRed' : ''].join(' ')}>{item.height} грамм</div>
                            </div>
                            <button className='btn' onClick={onClickPlus}>ЗАКАЗАТЬ</button>
                        </div>
                    </>
            }
        </div>
    );
};

export default Card;