import React, {useEffect, useState} from 'react';
import Card from "../compontents/Card";
import axios from "axios";
import {useOutletContext} from "react-router-dom";
import ContentLoader from "react-content-loader";
import LoaderCard from "../compontents/LoaderCard";

const Asort = () => {

    const [productBasket, setProductBasket] = useOutletContext()
    const [filterCategory, setFilterCategory] = useState(0)
    const [isLoading, setIsLoading] = useState(true)
    const [productFilter, setProductFilter] = useState('');
    const [itemFilter, setItemFilter] = useState([
        {id: 1, name: 'Суши'},
        {id: 2, name: 'Супы'},
        {id: 3, name: 'Салаты и Закуски'},
        {id: 4, name: 'Десерты'},
        {id: 5, name: 'Напитки'},
        {id: 6, name: 'Горячие блюда и гарниры'},
        {id: 7, name: 'Пица и бургеры'},
    ])
    const [product, setProduct] = useState([])
    let [clonePrice, setClonePrice] = useState(0)




    useEffect( () => {
        setIsLoading(true)
            async function axiosApp () {
                await axios.get(`https://6378a8e37eb4705cf2717c2a.mockapi.io/items?fitler=` + productFilter).then((res) => {
                    setProduct(res.data)
                })
                await axios.get('https://6378a8e37eb4705cf2717c2a.mockapi.io/cart').then((res) => {
                    setProductBasket(res.data)
                })
                setIsLoading(false)
            }
            axiosApp();
    }, [productFilter])


    const onAddProduct = (obj) => {
        const findCloneId = productBasket.find(prev => prev.parentId === obj.parentId)
        if (!findCloneId){
            axios.post('https://6378a8e37eb4705cf2717c2a.mockapi.io/cart', obj)
            setProductBasket([...productBasket ,obj])
        }
    }
    const filterProduct = (idx, item) => {
        setFilterCategory(idx)
        setProductFilter(item.id)
        console.log(item)
    }


    return (
        <div className='asort'>
            <ul className="filter">
                {
                    itemFilter.map((item, idx) =>
                    <li key={item.id} onClick={() => filterProduct(idx, item)} className={filterCategory === idx ? 'active' : ''}>
                        <button>{item.name}</button>
                    </li>
                    )
                }

            </ul>
            <div className="content-list container">
                {
                    product.map((item, idx) =>
                        <Card
                            key={idx}
                            img={item.img}
                            title={item.title}
                            text={item.text}
                            price={item.price}
                            height={item.height}
                            item={item}
                            onPlus={(obj) => onAddProduct(obj)}
                            isLoading={isLoading}
                        />
                    )
                }
            </div>
        </div>
    );
};

export default Asort;